import * as React from 'react';
import clsx from 'clsx';
import { Box, Tab, Tabs, withStyles, WithStyles, AppBar } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Loadable from '@loadable/component';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';
import { Link as RouterLink } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { withRouter, RouteComponentProps } from 'react-router';
import { RootState } from '../../reducers';
import {
	ChangeSectionAction,
	GetInvestorRelationsContentAction,
	Selections,
	SelectedCategoryAction,
	FileLink
} from '../../redux/Investors/types';
import Style from './style';
import { Resources } from '../../constants/resources';
import TabPanel from '../../components/TabPanel';
import { FeedbackFormState, UpdateSiteIdAction } from '../../redux/FeedbackForm/types';
import { ActionStatus } from '../../utils/functions';

const FeedbackForm = Loadable(() => import('../../components/FeedbackForm'));
const BackDrop = Loadable(() => import('../../components/BackDrop'));
const Footer = Loadable(() => import('../../components/Footer'));

interface Props extends WithStyles<typeof Style>, RouteComponentProps {
	feedbackForm: FeedbackFormState;
	updateSiteId: typeof UpdateSiteIdAction.action;
	kioskMode: boolean;
	isDesktopView: boolean;
}
interface State {
	selectedTab: number;
}
/**
 * Feedback page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class Feedback extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			selectedTab: 0
		};
	}

	componentDidMount() {
		// scrollIntoView(document.getElementById('home') as any);
	}

	componentDidUpdate() {}

	handleTabChange = (event: React.ChangeEvent<any>, newValue: number) => {
		this.setState({ selectedTab: newValue });
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, feedbackForm, isDesktopView, kioskMode } = this.props;
		const { selectedTab } = this.state;
		const { REACT_APP_CAPTACHA_KEY = '' } = process.env;
		return (
			<GoogleReCaptchaProvider reCaptchaKey={REACT_APP_CAPTACHA_KEY}>
				<div className={classes.content}>
					<BackDrop
						loading={[
							feedbackForm.getStoreList,
							feedbackForm.sendFeedbackData
						].includes(ActionStatus.LOADING)}
					/>
					{!kioskMode && (
						<div className={classes.block}>
							<div className={classes.largerFont}>{Resources.FeedbackTitle}</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								{Resources.FeedbackContent}
							</p>
						</div>
					)}
					{!kioskMode && (
						<AppBar
							position="static"
							color="default"
							className={classes.appBar}
							elevation={1}
						>
							<Tabs
								centered
								className={classes.tabs}
								onChange={this.handleTabChange}
								value={selectedTab}
								aria-label="simple tabs example"
								indicatorColor="primary"
								textColor="primary"
								variant="fullWidth"
								scrollButtons="auto"
								classes={{
									indicator: classes.indicator
								}}
								TabIndicatorProps={{ children: <span /> }}
							>
								<Tab
									color="primary"
									disableRipple
									label={
										<Box
											className={clsx(classes.tabFont, {
												[classes.activeTab]: selectedTab === 0
											})}
										>
											Customers
										</Box>
									}
								/>
								<Tab
									color="primary"
									disableRipple
									label={
										<Box
											className={clsx(classes.tabFont, {
												[classes.activeTab]: selectedTab === 1
											})}
										>
											Vendors
										</Box>
									}
								/>
								<Tab
									color="primary"
									disableRipple
									label={
										<Box
											className={clsx(classes.tabFont, {
												[classes.activeTab]: selectedTab === 2
											})}
										>
											employees
										</Box>
									}
								/>
								<Tab
									color="primary"
									disableRipple
									label={
										<Box
											className={clsx(classes.tabFont, {
												[classes.activeTab]: selectedTab === 3
											})}
										>
											others
										</Box>
									}
								/>
							</Tabs>
						</AppBar>
					)}
					{kioskMode && (
						<div className={classes.block}>
							<div className={clsx(classes.largerFont, classes.textCenter)}>
								{Resources.FeedbackTitle}
							</div>
						</div>
					)}
					<div className={classes.tabpanels}>
						<TabPanel value={selectedTab} index={0}>
							<FeedbackForm type={0} />
						</TabPanel>
						<TabPanel value={selectedTab} index={1}>
							<FeedbackForm type={1} />
						</TabPanel>
						<TabPanel value={selectedTab} index={2}>
							<FeedbackForm type={2} />
						</TabPanel>
						<TabPanel value={selectedTab} index={3}>
							<FeedbackForm type={3} />
						</TabPanel>
					</div>
				</div>
				{isDesktopView && <Footer />}
			</GoogleReCaptchaProvider>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	selections: state.investorRelations.selections,
	feedbackForm: state.feedbackReducer,
	kioskMode: state.feedbackReducer.kioskMode,
	isDesktopView: state.screenReducer.isDesktopView
});

const mapDispatchToProps = {
	getInvestorRelationsContent: GetInvestorRelationsContentAction.action,
	changeSection: ChangeSectionAction.action,
	selectedCategoryAction: SelectedCategoryAction.action,
	updateSiteId: UpdateSiteIdAction.action
};

export default withStyles(Style, { name: 'DmartCorpFreshFeedback' })(
	withRouter(connect(mapStateToProps, mapDispatchToProps)(Feedback))
);
